    color: #333;
}
.table th:first-child,
  .table td:first-child {
    padding-left: 10px;
}
h2 {
  margin: 2.5em 0 1em 0;
}
.content a {
  text-decoration: none;
  color: #409eff;
}
.demo-desc code {
  background-color: #e6effb;
  display: inline-block;
  margin: 0 0.2em;
  padding: 0.1em 0.4em;
  height: 1.1em;
  line-height: 1.1em;
}
#app .container {
  width: 1250px;
  margin: 0 auto;
  background: #fff;
  padding: 1em;
}

.menu-list {
  border-right: 1px solid #dddee1;
  list-style: none;
  margin: 0 1em 0 0;
  padding: 0;
  height: 100%;
}
.menu-list a {
    display: block;
    padding: 0.5em;
    text-decoration: none;
    color: #495060;
    font-size: 1.1em;
    margin: 0.5em 0;
}
.menu-list a:hover, .menu-list a.router-link-active {
      color: #409eff;
}

.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: #fff;
  margin-bottom: 1em;
  z-index: 1;
  -webkit-box-shadow: 0 0 0.5em #dddee1;
          box-shadow: 0 0 0.5em #dddee1;
}
.header .header__wrapper {
    width: 1250px;
    margin: 0 auto;
    height: 60px;
    position: relative;
}
.header .logo {
    position: relative;
    text-decoration: none;
    color: #409eff;
}
.header .logo h1 {
      display: inline-block;
      text-align: center;
      margin: 0;
      padding: 0;
      height: 60px;
      line-height: 60px;
}
.header .right {
    position: relative;
    float: right;
    height: 60px;
    display: table;
}
.header .right > * {
      display: table-cell;
      vertical-align: middle;
}

.demo-block {
  border: 1px solid #dddee1;
  font-size: 1em;
  -webkit-transition: -webkit-box-shadow 0.25s ease-in-out;
  transition: -webkit-box-shadow 0.25s ease-in-out;
  transition: box-shadow 0.25s ease-in-out;
  transition: box-shadow 0.25s ease-in-out, -webkit-box-shadow 0.25s ease-in-out;
}
.demo-block code {
    font-family: Menlo, Monaco, Consolas, Courier, monospace;
}
.demo-block:hover {
    -webkit-box-shadow: 0 0 0.5em #dddee1;
            box-shadow: 0 0 0.5em #dddee1;
}
.demo-block .demo-code {
    padding: 1em;
    border-bottom: 1px solid #dddee1;
    margin-bottom: 1em;
}
.demo-block .demo-bottom {
    padding: 0 1em;
    position: relative;
}
.demo-block .demo-bottom .demo-desc {
      padding-right: 3em;
      min-height: 3em;
}
.demo-block .demo-bottom .demo-desc p {
        line-height: 2em;
        margin: 0.5em 0;
}
.demo-block .demo-bottom .demo-icon {
      position: absolute;
      right: 0.5em;
      top: 0;
      cursor: pointer;
      font-size: 2.5em;
      color: #888;
      -webkit-transition: -webkit-transform 0.25s ease-in-out;
      transition: -webkit-transform 0.25s ease-in-out;
      transition: transform 0.25s ease-in-out;
      transition: transform 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out;
}
.demo-block .demo-source {
    border-top: 1px solid #dddee1;
}
.demo-block .demo-source pre {
      margin: 0;
}
.demo-block .slide-enter-active,
  .demo-block .slide-leave-active {
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    -webkit-transition: -webkit-transform 0.25s ease-in-out;
    transition: -webkit-transform 0.25s ease-in-out;
    transition: transform 0.25s ease-in-out;
    transition: transform 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out;
}
.demo-block .slide-enter,
  .demo-block .slide-leave-to {
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
}
.demo-block .slide-enter-to,
  .demo-block .slide-leave {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
}
